module.exports = "<template>\n\t<div class=\"previewTable\">\n\t\t<pkp-table v-bind=\"table\">\n\t\t\t<template slot-scope=\"{row, rowIndex}\">\n\t\t\t\t<table-cell\n\t\t\t\t\tv-for=\"(column, columnIndex) in table.columns\"\n\t\t\t\t\t:key=\"column.name\"\n\t\t\t\t\t:column=\"column\"\n\t\t\t\t\t:row=\"row\"\n\t\t\t\t\t:tabindex=\"!rowIndex && !columnIndex ? 0 : -1\"\n\t\t\t\t>\n\t\t\t\t\t<pkp-button\n\t\t\t\t\t\tv-if=\"column.name === 'pdf'\"\n\t\t\t\t\t\t:label=\"row.pdf.toString()\"\n\t\t\t\t\t\t@click=\"viewItem(row)\"\n\t\t\t\t\t/>\n\t\t\t\t</table-cell>\n\t\t\t</template>\n\t\t</pkp-table>\n\t</div>\n</template>\n\n<script>\nimport PkpTable from '@/components/Table/Table.vue';\nimport TableCell from '@/components/Table/TableCell.vue';\nimport PkpButton from '@/components/Button/Button.vue';\nimport {props} from '../config';\n\nexport default {\n\tcomponents: {\n\t\tPkpTable,\n\t\tTableCell,\n\t\tPkpButton\n\t},\n\tdata() {\n\t\treturn {\n\t\t\ttable: {\n\t\t\t\t...props,\n\t\t\t\tdescription:\n\t\t\t\t\t'This table uses the <code>rows</code> slot to customize the output of the PDF column in each row.',\n\t\t\t\trows: props.rows.slice(0, 10)\n\t\t\t}\n\t\t};\n\t},\n\tmethods: {\n\t\tviewItem: function(row) {\n\t\t\talert(JSON.stringify(row));\n\t\t}\n\t}\n};\n</script>\n"