module.exports = "<template>\n\t<!-- Use the v-bind syntax to bind all props at once. -->\n\t<list-panel v-bind=\"components.example\" @set=\"set\">\n\t\t<pkp-header slot=\"header\">\n\t\t\t{{ title }}\n\t\t\t<template slot=\"actions\">\n\t\t\t\t<pkp-button\n\t\t\t\t\t@click=\"toggleFilters\"\n\t\t\t\t\tlabel=\"Filters\"\n\t\t\t\t\ticon=\"filter\"\n\t\t\t\t\t:isActive=\"components.example.isSidebarVisible\"\n\t\t\t\t/>\n\t\t\t</template>\n\t\t</pkp-header>\n\t</list-panel>\n</template>\n\n<script>\nimport Container from '@/components/Container/Container.vue';\nimport PkpHeader from '@/components/Header/Header.vue';\nimport PkpButton from '@/components/Button/Button.vue';\nimport {props} from '../config';\nimport items from '../helpers/items';\n\nexport default {\n\textends: Container,\n\tcomponents: {\n\t\tPkpButton,\n\t\tPkpHeader\n\t},\n\tdata() {\n\t\treturn {\n\t\t\tcomponents: {\n\t\t\t\texample: {\n\t\t\t\t\t...props,\n\t\t\t\t\tid: 'example',\n\t\t\t\t\titems: items,\n\t\t\t\t\tfilters: [\n\t\t\t\t\t\t{\n\t\t\t\t\t\t\theading: 'Colors',\n\t\t\t\t\t\t\tfilters: [\n\t\t\t\t\t\t\t\t{\n\t\t\t\t\t\t\t\t\tparam: 'color',\n\t\t\t\t\t\t\t\t\ttitle: 'Red',\n\t\t\t\t\t\t\t\t\tvalue: 'red'\n\t\t\t\t\t\t\t\t},\n\t\t\t\t\t\t\t\t{\n\t\t\t\t\t\t\t\t\tparam: 'color',\n\t\t\t\t\t\t\t\t\ttitle: 'Green',\n\t\t\t\t\t\t\t\t\tvalue: 'green'\n\t\t\t\t\t\t\t\t},\n\t\t\t\t\t\t\t\t{\n\t\t\t\t\t\t\t\t\tparam: 'color',\n\t\t\t\t\t\t\t\t\ttitle: 'Blue',\n\t\t\t\t\t\t\t\t\tvalue: 'blue'\n\t\t\t\t\t\t\t\t}\n\t\t\t\t\t\t\t]\n\t\t\t\t\t\t}\n\t\t\t\t\t]\n\t\t\t\t}\n\t\t\t},\n\t\t\ttitle: 'List Panel with Filters'\n\t\t};\n\t},\n\tmethods: {\n\t\ttoggleFilters() {\n\t\t\tthis.components.example.isSidebarVisible = !this.components.example\n\t\t\t\t.isSidebarVisible;\n\t\t}\n\t}\n};\n</script>\n"